import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import HomeView from "../views/HomeView.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/host",
    name: "LiveHost",
    component: () =>
      import(/* webpackChunkName: "live" */ "../views/Host.vue"),
  },
  {
    path: "/hostList",
    name: "HostList",
    component: () =>
      import(/* webpackChunkName: "live" */ "../views/HostList.vue"),
  },
  {
    path: "/view/:roomId",
    name: "LiveViewer",
    component: () =>
      import(/* webpackChunkName: "live" */ "../views/Viewer.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
